<script>
    import { afterUpdate } from "svelte";
    import FloatButton from "../Components/FloatButton.svelte";
    import FloatNotification from "../Components/FloatNotification.svelte";
    import PopUp from "../Components/PopUp.svelte";
    import EditDialog from "../Components/ModalDialogs/EditDialog.svelte";
    import WorkerHistoryDialog from "../Components/ModalDialogs/WorkerHistoryDialog.svelte";
    import {
        getCollection,
        uploadRowChanges,
        getWorkerHistoryById
    } from "../js/requests.js";
    import { download, tableToCSV } from "../js/functions.js";
    import { currentTable } from "../stores.js";
    import cloneDeep from "lodash.clonedeep";
    import { navigate } from "svelte-routing";

    let table;
    let tableName;
    let canBeDeletedOrInserted = false;

    let editHistory = {};
    let keys = [];
    let columns = [];
    let rows = [];
    let numOfColumns;
    let containsGeo = false;

    let rowToEdit;
    let selectedIndexes = {};

    let rowsToEdit = {};
    let rowsToAdd = {};
    let rowsToDelete = {};

    let popupX = 0;
    let popupY = 0;
    let popupVisible = false;

    let foreignKey = "";

    function reinit() {
        rowsToAdd = {};
        rowsToEdit = {};
        rowsToDelete = {};
        selectedIndexes = {};
        popupX = 0;
        popupY = 0;
        popupVisible = false;
    }
    let unsubscribe = currentTable.subscribe(t => {
        tableName = t["metadata"]["Name"];
        containsGeo = t["metadata"]["containsGeo"];
        keys = t["metadata"]["keys"];
        canBeDeletedOrInserted = t["metadata"]["canBeDeletedOrInserted"];
        foreignKey = t["metadata"]["foreignKey"];
        table = t["table"];
        rows = [];
        rows.push(keys);
        table.forEach(element => {
            columns = [];
            for (let [key, value] of Object.entries(element)) {
                columns.push(value);
            }
            rows.push(columns);
        });
        console.log(rows);
        numOfColumns = columns.length;
        reinit();
        rows = rows;
    });

    let isSelectable = true; /* defines if user can select text with cursor (purpose is to block selecting text when float butoon is moving) */

    let ctrlPressed = false; /* multiple row selection is enabled when ctrl is pressed */
    window.onkeydown = e => {
        if (e.keyCode === 17) {
            ctrlPressed = true;
        }
    };
    window.onkeyup = e => {
        if (e.keyCode === 17) {
            ctrlPressed = false;
        }
    };

    const showPopUp = (row, index) => event => {
        if (ctrlPressed) {
            if (selectedIndexes[index]) {
                delete selectedIndexes[index];
                selectedIndexes = selectedIndexes;
                rowToEdit = [];
                return;
            } else {
                selectedIndexes[index] = true;
                rowToEdit = row;
            }
        } else {
            selectedIndexes = {};
            selectedIndexes[index] = true;
            rowToEdit = row;
        }
        popupVisible = false;
        popupVisible = true;
        popupX = event.pageX;
        popupY = event.pageY;
        rows = rows;
    };

    let editDialogOpened = false;
    let isAddDialog = false;
    /* edit dialog and add dialog are essentialy the same but edit dialog comes with filled inputs whereas add dialog not
    so they are actually the same element*/

    /* get index of first selected row */
    function getFirstSelectedIndex() {
        return parseInt(Object.keys(selectedIndexes)[0]);
    }

    /* get index of last selected row */
    function getLastSelectedIndex() {
        const keys = Object.keys(selectedIndexes);
        return parseInt(keys[keys.length - 1]);
    }

    function handleGoHome() {
        if (
            confirm(
                "You are trying to go home. All unsaved changes will be lost"
            )
        ) {
            navigate("/home", {});
        }
    }

    /*loads changes to the server*/
    function handleUpload() {
        let all = [rowsToAdd, rowsToEdit];
        let resAll = [[], [], []];
        for (let i = 0; i < all.length; i++) {
            for (let [key, value] of Object.entries(all[i])) {
                if (value && rows[key] != [] && rows[key] != null) {
                    let row = rows[key];
                    let res = {};
                    keys.forEach((key, i) => (res[key] = row[i]));
                    res["ID"] = parseInt(row[0]);
                    resAll[i].push(res);
                }
            }
        }
        let toDelete = Object.keys(rowsToDelete)
            .filter(t => rowsToDelete[t])
            .map(k => parseInt(rows[k][0]));
        uploadRowChanges(tableName, resAll[0], resAll[1], toDelete)
            .then(() => {
                setTimeout(() => {
                    getCollection(tableName).catch(() => console.log("kek"));
                }, 10);
            })
            .catch(() => console.log("lol"));
    }

    function handleOpenEditDialog() {
        if (!editDialogOpened) {
            editDialogOpened = true;
        } else {
            editDialogOpened = false;
        }
    }

    function handleOpenAddDialog() {
        isAddDialog = true;
        rowToEdit = [];
        handleOpenEditDialog();
    }

    function handleAddRow(event) {
        isAddDialog = false;
        let index = rows.length;
        index = Number.isNaN(index) ? 1 : index;
        console.log(index);
        rows.splice(index, 0, event.detail.row);
        rows = rows;
        rowsToAdd[index] = true;
        console.log(rowsToAdd);
    }

    function handleDeleteRow() {
        for (let [key, value] in selectedIndexes) {
            if (rowsToDelete[key]) {
                delete rowsToDelete[key];
                if (key in rowsToAdd) {
                    rowsToAdd[key] = true;
                }
                if (key in rowsToEdit) {
                    rowsToEdit[key] = true;
                }
                rowsToDelete = rowsToDelete;
            } else {
                rowsToDelete[key] = true;
                if (key in rowsToAdd) {
                    rowsToAdd[key] = false;
                }
                if (key in rowsToEdit) {
                    rowsToEdit[key] = false;
                }
            }
        }
    }

    function handleSaveEditRow(event) {
        let index = getFirstSelectedIndex();
        if (!(index in rowsToAdd)) {
            rowsToEdit[index] = true;
        }
        rowsToDelete[index] = false;
        if (!(index in editHistory)) {
            editHistory[index] = cloneDeep(rows[index]);
        }
        rows[index] = event.detail.row;
    }

    function handleCopyRow() {
        const el = document.createElement("textarea");
        el.value = selectedIndexes.keys;
        document.body.appendChild(el);
        el.select();
        document.execCommand("copy");
        document.body.removeChild(el);
    }

    function handleUndo() {
        const selectedKeys = Object.keys(selectedIndexes);
        selectedKeys.forEach(key => {
            if (key in editHistory) {
                delete rowsToEdit[key];
                rows[key] = editHistory[key];
                delete editHistory[key];
            }
        });
        rowsToEdit = rowsToEdit;
    }
    let workerHistoryDialogOpened = false;
    let workerRecords = [];
    function handleShowWorkerRecords(id) {
        getWorkerHistoryById(id).then(res => {
            workerRecords = res;
            workerHistoryDialogOpened = true;
        });
    }

    function handleDownload() {
        download(tableName + ".csv", tableToCSV(rows));
    }
</script>

<style>
    span {
        max-width: 99%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        height: fit-content;
    }
    main {
        text-align: center;
        padding: 1em;
        margin: 0 auto;
    }

    .not-selectable {
        user-select: none;
    }

    .tr {
        display: flex;
        justify-content: space-between;
        border-right: 1px solid #f1f1f1;
    }

    .table,
    .td,
    .th {
        text-align: left;
    }

    .table {
        border-collapse: collapse;
        width: 100%;
        border-bottom: 1px solid #f1f1f1;
    }
    .th {
        background-color: #e0e0e0;
        font-weight: bold;
        color: black;
    }

    .th,
    .td {
        width: 100%;
        border-left: 1px solid #f1f1f1;
        min-height: 40px;
        min-width: 40px;
        padding: 7px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        white-space: nowrap;
    }

    .td {
        height: 37px;
    }

    .row-selected {
        border-left: 3px dashed darkblue;
        border-right: 3px dashed darkblue;
    }
    .top-row-selected {
        border-top: 3px dashed darkblue;
    }
    .bottom-row-selected {
        border-bottom: 3px dashed darkblue;
    }

    .row-to-add {
        background-color: #35cc62 !important;
        color: white;
    }

    .row-to-edit {
        background-color: #ffeebc !important;
    }

    .row-to-delete {
        background-color: #f18c8c !important;
    }

    .even-row {
        background-color: #f8f8f8;
    }

    .link {
        color: blue;
        text-decoration: underline;
    }
</style>

<main class:not-selectable={!isSelectable}>
    <WorkerHistoryDialog
        bind:isOpened={workerHistoryDialogOpened}
        items={workerRecords} />
    <EditDialog
        bind:isOpened={editDialogOpened}
        isAdd={isAddDialog}
        {keys}
        values={rows[getFirstSelectedIndex()]}
        withPositionPicker={containsGeo}
        on:close={() => {
            popupVisible = false;
        }}
        on:save={handleSaveEditRow}
        on:add={handleAddRow} />
    <PopUp
        x={popupX}
        y={popupY}
        visible={popupVisible}
        deleteInsertEnabled={canBeDeletedOrInserted}
        on:edit={() => {
            editDialogOpened = true;
        }}
        on:copy={handleCopyRow}
        on:add={handleOpenAddDialog}
        on:delete={handleDeleteRow}
        on:undo={handleUndo}
        on:hide={() => {
            popupVisible = false;
            selectedIndexes = [];
        }} />
    <div class="table">
        {#each rows as row, i}
            <div
                class="tr"
                class:even-row={i % 2 == 0}
                class:top-row-selected={!selectedIndexes[i - 1] && selectedIndexes[i]}
                class:row-selected={selectedIndexes[i]}
                class:bottom-row-selected={!selectedIndexes[i + 1] && selectedIndexes[i]}
                class:row-to-add={rowsToAdd[i]}
                class:row-to-edit={rowsToEdit[i]}
                class:row-to-delete={rowsToDelete[i]}>
                {#each row as column, j}
                    {#if i == 0}
                        <div class="th">
                            <span>{column}</span>
                        </div>
                    {:else if rows[0][j] === foreignKey}
                        <div class="td">
                            <span
                                on:click={() => {
                                    handleShowWorkerRecords(column);
                                }}
                                class="link">
                                {column}
                            </span>
                        </div>
                    {:else}
                        <div class="td" on:click={showPopUp(row, i)}>
                            <span>{column}</span>
                        </div>
                    {/if}
                {/each}
            </div>
        {/each}
    </div>
    <FloatButton
        insertEnabled={canBeDeletedOrInserted}
        on:addClick={handleOpenAddDialog}
        on:homeClick={handleGoHome}
        on:uploadClick={handleUpload}
        on:downloadClick={handleDownload}
        on:moving={() => {
            isSelectable = false;
        }}
        on:stoppedmoving={() => {
            isSelectable = true;
        }} />
</main>
