<script>
    export let isOpened = false;
    export let setCors = () => {};
    import ModalDialog from "./ModalDialog.svelte";
    import Map from "../Map.svelte";

    let getCors = () => {};
</script>

<style>
    .submit {
        background-image: linear-gradient(
            rgba(52, 206, 87, 1),
            rgba(40, 167, 69, 1)
        );
        border: 1px solid #258c3e;
        color: white;
        float: right;
    }
    .submit:hover {
        cursor: pointer;
    }
</style>

<ModalDialog bind:isOpened width="600px" top="2%">
    <h2 slot="header">Picker</h2>
    <div slot="body" style="height:500px; width:100%">
        <Map
            on:mount={event => {
                getCors = event.detail.getCors;
            }} />
    </div>
    <div slot="footer">
        <button
            class="submit"
            on:click={() => {
                setCors(getCors());
            }}>
            Pick Cors
        </button>
    </div>
</ModalDialog>
