<script>
    import CreateTableDialog from "../Components/ModalDialogs/CreateTableDialog.svelte";
    import { getCollection, getCollectionNames } from "../js/requests.js";
    import { navigate } from "svelte-routing";
    import { login } from "../stores.js";

    let collectionNames = [];

    const update = () => {
        getCollectionNames().then(res => (collectionNames = res));
    };

    update();

    function handleOpenCollection(collectionName) {
        getCollection(collectionName).then(res =>
            navigate("/table", { replace: false })
        );
    }

    let isCreateTableDialogOpened = false;
    function handleCreateTableDialogOpened() {
        isCreateTableDialogOpened = true;
    }
</script>

<style>
    span {
        text-align: center;
    }
    img {
        transform: translateZ(0);
    }
    button {
        border-radius: 50%;
        background: transparent;
        border: none;
    }
    button:focus {
        outline: none;
    }

    header {
        position: fixed;
        left: 0;
        right: 0;
        height: 64px;
        width: 100%;
        color: white;
        background-color: #24292e;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        font-weight: 500;
        z-index: 2;
    }

    .header-left {
        margin-left: 20px;
    }

    .header-right {
        margin-right: 20px;
    }

    .header-right > button {
        height: 33px;
        width: 33px;
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.3s ease-in-out;
    }

    .header-right > button:hover {
        background-color: rgba(190, 191, 193, 0.2);
    }

    .header-right > button > img {
        height: 60%;
        width: 60%;
    }

    .collections-list {
        padding-top: 70px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }
    .collection-link-container {
        padding: 5px;
        font-size: 20px;
        font-weight: 500;
        color: #24292e;
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    .collection-link-container > img {
        height: 64px;
        width: 64px;
    }
    .collection-link-container > span {
        margin-left: 10px;
    }
    .collection-link-container:hover {
        mix-blend-mode: luminosity;
        background-color: #f5f5f5;
    }
</style>

<div style="height:100%;width:100%;">
    <CreateTableDialog
        bind:isOpened={isCreateTableDialogOpened}
        updateParent={update} />
    <header>
        <div class="header-left">
            <span>Logged as: {$login}</span>
        </div>

        <div class="header-right">
            <button on:click={handleCreateTableDialogOpened}>
                <img src="images/plus.png" alt="new table" title="new table" />
            </button>
        </div>
    </header>
    <div class="collections-list">
        {#each collectionNames as collection}
            <div
                class="collection-link-container"
                on:click={() => {
                    handleOpenCollection(collection);
                }}>
                <img src="/images/table.png" alt="table" />
                <span>{collection}</span>
            </div>
        {/each}
    </div>
</div>
