<script>
    export let isOpened = false;
    export let updateParent = () => {};

    import ModalDialog from "./ModalDialog.svelte";
    import { getRequiredFields, createCustomTable } from "../../js/requests.js";

    let columns = [];
    getRequiredFields().then(res => {
        columns = res.map(el => ({ value: el, required: true }));
    });

    function handleCreateNewRow() {
        columns.push({ value: "", required: false });
        columns = columns;
    }

    function handleSubmit() {
        const name = prompt("Enter table name");
        if (name != null && name != "") {
            let keys = columns.map(col => col.value);
            createCustomTable({ Name: name, keys }).then(() => {
                updateParent();
                isOpened = false;
            });
        }
    }

    function deleteColumn(index) {
        if (!columns[index].required) {
            columns.splice(index, 1);
            columns = columns;
        }
    }
</script>

<style>
    ul {
        list-style: none;
        margin-top: 0;
        padding-inline-start: 0;
    }
    li {
        display: flex;
        flex-direction: row;
        margin: 10px 0;
    }
    input {
        margin: auto 10px;
    }
    .delete-button {
        width: 36.4px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 2px;
    }
    .delete-button:hover {
        background-color: #c4c4c4;
        transition: 0.3s ease-in-out;
    }
    .delete-button > img {
        height: 20px;
        margin: auto;
    }
    .submit {
        background-image: linear-gradient(
            rgba(52, 206, 87, 1),
            rgba(40, 167, 69, 1)
        );
        border: 1px solid #258c3e;
        color: white;
        float: right;
    }

    .submit:hover {
        background-image: linear-gradient(
            rgba(52, 206, 87, 1),
            rgba(52, 206, 87, 1)
        );
    }
    .footer {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
</style>

<ModalDialog bind:isOpened width="300px">
    <h2 slot="header">Create Table</h2>
    <ul slot="body">
        {#each columns as column, i}
            <li>
                <input
                    placeholder="New row"
                    disabled={column.required}
                    bind:value={column.value} />
                {#if !column.required}
                    <div
                        class="delete-button"
                        on:click={() => {
                            deleteColumn(i);
                        }}>
                        <img src="images/delete.png" alt="delete" />
                    </div>
                {/if}
            </li>
        {/each}
    </ul>
    <div class="footer" slot="footer">
        <div class="footer-left">
            <button on:click={handleCreateNewRow} class="submit">
                New row
            </button>
        </div>
        <div class="footer-right">
            <button on:click={handleSubmit} class="submit">Submit</button>
        </div>
    </div>
</ModalDialog>
