<script>
    import { navigate } from "svelte-routing";

    import { getJwt } from "../js/requests.js";
    import { jwt, login } from "../stores.js";

    let username;
    let password;

    let error = false;

    function handleSubmit() {
        getJwt(username, password)
            .then(res => {
                jwt.update(jwt => res["access_token"]);
                login.update(l => res.username);
                setTimeout(() => {
                    jwt.update(jwt => "");
                    navigate("/", { replace: "false" });
                }, (res["expiration_time"] - 1) * 1000);
                navigate("/home", { replace: "false" });
            })
            .catch(res => {
                error = true;
            });
    }
</script>

<style>
    .container {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #f5f5f5f5;
    }
    .card {
        width: 50%;
        height: 50%;
        max-width: 530px;
        min-height: 336px;
        background-color: white;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 0 20px;
    }
    h1 {
        text-align: center;
    }
    .card > * {
        width: 100%;
        height: 100%;
    }
    .body {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    input {
        width: 100%;
        height: 60px;
    }
    .footer {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    button {
        height: 50%;
        font-size: 20px;
        padding-left: 30px;
        padding-right: 30px;
    }
    .error {
        border: 1px solid red;
    }
</style>

<div class="container">
    <div class="card">
        <div class="header">
            <h1>Login</h1>
        </div>
        <div class="body">
            <input
                class:error
                type="text"
                bind:value={username}
                placeholder="Login" />
            <input
                class:error
                type="password"
                bind:value={password}
                placeholder="Password" />
        </div>
        <div class="footer">
            <button on:click={handleSubmit}>Submit</button>
        </div>
    </div>
</div>
