<script>
    import { createEventDispatcher } from "svelte";
    import { spring } from "svelte/motion";
    import { pannable } from "../js/pannable.js";

    let panstartDate;
    const dispatch = createEventDispatcher();
    const coords = spring(
        { x: 0, y: 0 },
        {
            stiffness: 1,
            damping: 1
        }
    );
    let isMoving = false;
    function handlePanStart() {
        panstartDate = new Date().getTime();
        isMoving = true;
        dispatch("moving", {});
    }

    function handlePanMove(event) {
        coords.update($coords => ({
            x: $coords.x + event.detail.dx,
            y: $coords.y + event.detail.dy
        }));
    }

    function handlePanEnd(event) {
        if (new Date().getTime() - panstartDate < 500) {
            dispatch("click", {});
        }
        isMoving = false;
        dispatch("stoppedmoving", {});
    }
</script>

<style>
    .box {
        position: absolute;
        width: 5px;
        height: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background: transparent;
        cursor: pointer;
        box-shadow: 0 0 0 rgba(204, 169, 44, 0.4);
        animation: pulse 1s infinite;
    }
    .box > img {
        width: 30px;
        height: 30px;
    }

    @-webkit-keyframes pulse {
        0% {
            -webkit-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4);
        }
        70% {
            -webkit-box-shadow: 0 0 0 30px rgba(204, 169, 44, 0);
        }
        100% {
            -webkit-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
        }
    }
    @keyframes pulse {
        0% {
            -moz-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4);
            box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4);
        }
        70% {
            -moz-box-shadow: 0 0 0 30px rgba(204, 169, 44, 0);
            box-shadow: 0 0 0 30px rgba(204, 169, 44, 0);
        }
        100% {
            -moz-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
            box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
        }
    }
</style>

<div
    class="box"
    class:box-moving={isMoving}
    class:box-idle={!isMoving}
    use:pannable
    on:panstart={handlePanStart}
    on:panmove={handlePanMove}
    on:panend={handlePanEnd}
    style="transform: translate({$coords.x}px,{$coords.y}px);">
    <img src="images/alert.png" alt="kek" draggable="false" />
</div>
