<script>
    export let insertEnabled = false;
    import { createEventDispatcher } from "svelte";
    import { spring } from "svelte/motion";
    import { pannable } from "../js/pannable.js";

    let panstartDate;
    const dispatch = createEventDispatcher();
    const coords = spring(
        { x: 0, y: 0 },
        {
            stiffness: 1,
            damping: 1
        }
    );
    let isMoving = false;
    function handlePanStart() {
        panstartDate = new Date().getTime();
        isMoving = true;
        dispatch("moving", {});
    }
    let subBoxY = [0, 0, 0, 0];
    function handlePanMove(event) {
        coords.update($coords => ({
            x: $coords.x + event.detail.dx,
            y: $coords.y + event.detail.dy
        }));
    }

    function handlePanEnd(event) {
        isMoving = false;
        dispatch("stoppedmoving", {});
    }

    function checkIfClick() {
        return new Date().getTime() - panstartDate < 500;
    }

    function handleMainClick() {
        if (checkIfClick()) {
            if (subBoxY[0] == 0) {
                subBoxY = [-60, -120, -180, -240];
            } else {
                subBoxY = [0, 0, 0, 0];
            }
        }
    }

    function handleHomeClick() {
        if (checkIfClick()) {
            dispatch("homeClick", {});
        }
    }

    function handleAddClick() {
        if (checkIfClick()) {
            dispatch("addClick", {});
        }
    }

    function handleUploadClick() {
        if (checkIfClick) {
            dispatch("uploadClick", {});
        }
    }

    function handleDownloadClick() {
        if (checkIfClick) {
            dispatch("downloadClick", {});
        }
    }
</script>

<style>
    .box {
        position: fixed;
        left: 40px;
        top: 85%;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: box-shadow 1s;
        z-index: 1000;
    }
    .box-moving {
        cursor: grab;
    }
    .box-idle {
        cursor: pointer;
    }

    .main-box {
        width: 60px;
        height: 60px;
        background-color: rgb(193, 193, 193);
    }
    .main-box > img {
        transition: transform 0.8s ease-in-out;
    }
    .main-box:hover {
        box-shadow: 0 0 0 20px rgba(193, 193, 193, 0.5);
    }
    .main-box > img {
        height: 30px;
        width: 30px;
    }
    .main-box:hover img {
        transform: rotate(360deg);
    }

    .sub-box {
        width: 40px;
        height: 40px;
    }

    .sub-box > img {
        transform: translateZ(0);
        height: 20px;
        width: 20px;
    }

    .green-box {
        transition: box-shadow 1s, transform 0.3s ease-in-out;
        background-color: rgb(52, 168, 83);
    }
    .green-box > img {
        transition: transform 0.8s ease-in-out;
    }
    .green-box:hover {
        box-shadow: 0 0 0 15px rgba(52, 168, 83, 0.5);
    }
    .green-box:hover img {
        transform: rotate(360deg);
    }

    .blue-box {
        transition: box-shadow 1s, transform 0.2s ease-in-out;
        background-color: rgb(21, 137, 201);
    }

    .blue-box:hover {
        transition: box-shadow 1s;
        box-shadow: 0 0 0 15px rgba(21, 137, 201, 0.5);
    }

    .blue-box > img {
        transition: 0.2s;
    }

    .blue-box:hover img {
        transform: scale(1.2) translateZ(0);
    }

    .orange-box {
        transition: box-shadow 1s, transform 0.3s ease-in-out;
        background-color: rgb(249, 204, 157);
    }
    .orange-box:hover {
        box-shadow: 0 0 0 15px rgb(249, 204, 157, 0.5);
    }
    .orange-box > img {
        transition: 0.2s;
    }
    .orange-box:hover img {
        transform: scale(1.2) translateZ(0);
    }
</style>

{#if insertEnabled}
    <div
        class="box sub-box green-box"
        class:box-moving={isMoving}
        class:box-idle={!isMoving}
        use:pannable
        on:panstart={handlePanStart}
        on:panmove={handlePanMove}
        on:panend={handlePanEnd}
        on:click={handleAddClick}
        style={`transform:
        translate(${$coords.x + 10}px,${$coords.y + subBoxY[3]}px);`}>
        <img src="images/plus.png" alt="plus" draggable="false" />
    </div>
{/if}
<div
    class="box sub-box orange-box"
    class:box-moving={isMoving}
    class:box-idle={!isMoving}
    use:pannable
    on:panstart={handlePanStart}
    on:panmove={handlePanMove}
    on:panend={handlePanEnd}
    on:click={handleHomeClick}
    style={`transform:
    translate(${$coords.x + 10}px,${$coords.y + subBoxY[2]}px);`}>
    <img src="images/house.png" alt="home" draggable="false" />
</div>

<div
    class="box sub-box blue-box"
    class:box-moving={isMoving}
    class:box-idle={!isMoving}
    use:pannable
    on:panstart={handlePanStart}
    on:panmove={handlePanMove}
    on:panend={handlePanEnd}
    on:click={handleUploadClick}
    style={`transform:
    translate(${$coords.x + 10}px,${$coords.y + subBoxY[1]}px);`}>
    <img src="images/upload.png" alt="upload" draggable="false" />
</div>

<div
    class="box sub-box blue-box"
    class:box-moving={isMoving}
    class:box-idle={!isMoving}
    use:pannable
    on:panstart={handlePanStart}
    on:panmove={handlePanMove}
    on:panend={handlePanEnd}
    on:click={handleDownloadClick}
    style={`transform:
    translate(${$coords.x + 10}px,${$coords.y + subBoxY[0]}px);`}>
    <img src="images/download.png" alt="download" draggable="false" />
</div>

<div
    class="box main-box"
    class:box-moving={isMoving}
    class:box-idle={!isMoving}
    use:pannable
    on:panstart={handlePanStart}
    on:panmove={handlePanMove}
    on:panend={handlePanEnd}
    on:click={handleMainClick}
    style="transform: translate({$coords.x}px,{$coords.y}px);">
    <img src="images/settings.png" alt="kek" draggable="false" />
</div>
