<script>
    export let isOpened = false;
    export let isAdd = false;
    export let withPositionPicker = false;
    export let keys = [];
    export let values = [];

    import { createEventDispatcher } from "svelte";
    import ModalDialog from "./ModalDialog.svelte";
    import MapPickerDialog from "./MapPickerDialog.svelte";

    import cloneDeep from "lodash.clonedeep";

    const dispatch = createEventDispatcher();
    let newValues;
    function setNewValues(values) {
        newValues =
            Array.isArray(values) && values.length === keys.length
                ? cloneDeep(values)
                : Array(keys.length).fill("");
    }
    $: setNewValues(values);
    $: display = isOpened ? "block" : "none";
    let displayMap = false;

    const setCors = ({ lat, lng }) => {
        newValues[keys.indexOf("Latitude")] = lat;
        newValues[keys.indexOf("Longitude")] = lng;
        newValues = [...newValues];
    };

    function handleMapOpen() {
        if (displayMap) {
            displayMap = false;
        } else {
            displayMap = true;
        }
    }

    function handleDialogClose() {
        dispatch("close", {});
    }

    function handleSave() {
        dispatch("close", {});
        if (isAdd) {
            dispatch("add", { row: newValues });
        } else {
            dispatch("save", { row: newValues });
        }
    }
</script>

<style>
    h2 {
        margin-top: 0;
    }
    .body {
        overflow-x: scroll;
        max-width: 900px;
    }
    table,
    td,
    th {
        border: 1px solid #ddd;
        text-align: left;
    }

    table {
        border-collapse: collapse;
    }
    th {
        background-color: #1a73e8;
        color: white;
    }

    th,
    td {
        width: auto;
        min-height: 40px;
        min-width: 40px;
        padding: 7px;
        white-space: nowrap;
    }

    footer {
        height: 37px;
        margin: 20px 0;
        padding: 0 20px;
        background-color: white;
        transition: 1s;
    }

    button {
        width: 100px;
        white-space: nowrap;
        border-radius: 4px;
    }

    .submit {
        background-image: linear-gradient(
            rgba(52, 206, 87, 1),
            rgba(40, 167, 69, 1)
        );
        border: 1px solid #258c3e;
        color: white;
        float: right;
    }

    .submit:hover {
        background-image: linear-gradient(
            rgba(52, 206, 87, 1),
            rgba(52, 206, 87, 1)
        );
    }

    .primary {
        border: none;
        margin: 0 5px;
        /* background-image: linear-gradient(rgba(65,165,238,1), rgba(26,115,132,1)); */
        background-color: #1867c0;
        color: white;
        float: right;
    }

    .primary:hover {
        background-color: #2a73c5;
    }

    button:hover {
        cursor: pointer;
    }
</style>

<ModalDialog
    bind:isOpened
    width={(keys.length > 4 ? 4 : keys.length) * 200 + 'px'}>
    <h2 slot="header">{isAdd ? 'Add' : 'Edit'} Dialog</h2>
    <div class="body" slot="body">
        <table>
            <tr>
                {#each keys as key}
                    <th>{key}</th>
                {/each}
            </tr>
            <tr>
                {#each newValues as value, i}
                    <td>
                        <input
                            disabled={i === 0 && !isAdd}
                            type="text"
                            bind:value />
                    </td>
                {/each}
            </tr>
        </table>
    </div>
    <footer slot="footer">
        <button class="submit" on:click={handleSave}>Save</button>
        {#if withPositionPicker}
            <button class="primary" on:click={handleMapOpen}>
                {displayMap ? 'Hide Map' : 'Use Map'}
            </button>
        {/if}
    </footer>
</ModalDialog>
<MapPickerDialog bind:isOpened={displayMap} {setCors} />
