<script>
    export let x;
    export let y;
    export let visible;
    export let deleteInsertEnabled = false;

    import { onMount } from "svelte";
    import { createEventDispatcher } from "svelte";

    const dispatch = createEventDispatcher();
    let width = 184;
    let height = 35;

    $: visibility = visible ? "visible" : "hidden";
    onMount(async () => {
        const width = document.getElementsByClassName("menu-options")[0]
            .offsetWidth;
    });

    function handleEdit() {
        dispatch("edit", {});
    }

    function handleCopy() {
        dispatch("copy", {});
    }

    function handleAdd() {
        dispatch("add", {});
    }

    function handleDelete() {
        dispatch("delete", {});
    }

    function handleUndo() {
        dispatch("undo", {});
    }

    function handleHide() {
        dispatch("hide", {});
    }

    function handleCut() {
        handleCopy();
        handleDelete();
    }
</script>

<style>
    .menu {
        position: absolute;
        z-index: 10;
    }
    .menu-options {
        display: block;
        margin: 0;
        padding: 0;
        display: flex;
    }
    li {
        list-style: none;
        color: white;
        background-color: #d3d3d3;
        height: 30px;
        display: flex;
        align-items: center;
        padding: 0 5px;
    }
    li:hover {
        background-color: #969696;
        cursor: pointer;
    }
    img {
        height: 20px;
        width: 20px;
    }
    li:first-child {
        border-radius: 5px 0 0 5px;
    }
    li:last-child {
        border-radius: 0 5px 5px 0;
    }
    .menu::after {
        content: "";
        position: absolute;
        top: 100%;
        left: 50%;
        margin-left: -5px;
        border-width: 5px;
        border-style: solid;
        border-color: #d3d3d3 transparent transparent transparent;
    }
</style>

<div
    class="menu"
    style={`top:${y - height}px;left:${x - width / 2}px;visibility:${visibility}`}>
    <ul class="menu-options">
        <li class="menu-option" on:click={handleEdit}>
            <img src="/images/pencil.png" alt="edit" title="edit" />
        </li>
        <li class="menu-option" on:click={handleCopy}>
            <img src="/images/copy.png" alt="copy" title="copy" />
        </li>
        {#if deleteInsertEnabled}
            <li class="menu-option" on:click={handleAdd}>
                <img
                    src="/images/thin-plus.png"
                    alt="add"
                    title="add based on current" />
            </li>
            <li class="menu-option" on:click={handleCut}>
                <img src="/images/scissors.png" alt="cut" title="cut" />
            </li>
            <li class="menu-option" on:click={handleDelete}>
                <img src="/images/delete.png" alt="delete" title="delete" />
            </li>
        {/if}
        <li class="menu-option" on:click={handleUndo}>
            <img src="/images/undo.png" alt="undo" title="undo " />
        </li>
        <li class="menu-option" on:click={handleHide}>
            <img src="/images/hide.png" alt="hide" title="hide" />
        </li>
    </ul>
</div>
