<script>
    export let isOpened = false;
    export let width = "50%";
    export let top = "7%";

    import { createEventDispatcher } from "svelte";

    const dispatch = createEventDispatcher();
    $: display = isOpened ? "block" : "none";
    function handleDialogClose() {
        dispatch("close", {});
        isOpened = false;
    }
</script>

<style>
    header {
        padding: 15px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 51px;
    }

    h2 {
        margin-top: 0;
    }

    .modal {
        display: none; /* Hidden by default */
        position: fixed; /* Stay in place */
        z-index: 1000; /* Sit on top */
        left: 0;
        top: 0;
        width: 100%; /* Full width */
        height: 100%; /* Full height */
        overflow: auto; /* Enable scroll if needed */
        background-color: rgb(0, 0, 0); /* Fallback color */
        background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
    }

    /* Modal Content/Box */
    .modal-content {
        transition: 1s;
        background-color: #fefefe;
        margin: 7% auto; /* 7% from the top and centered */
        border: 1px solid #888;
        border-radius: 4px;
        /* width: 80%; */ /* Could be more or less, depending on screen size */
    }

    /* The Close Button */
    .close {
        height: fit-content;
        line-height: 1;
        padding: 8px 16px;
        color: #aaa;
        font-size: 28px;
        font-weight: bold;
        float: right;
    }

    .close:hover,
    .close:focus {
        color: black;
        text-decoration: none;
        cursor: pointer;
    }

    .modal-body {
        margin: 0 auto;
        max-width: 90%;
        transition: 1s;
    }

    footer {
        height: 37px;
        margin: 20px 0;
        padding: 0 20px;
        background-color: white;
    }
</style>

<div id="modal" style={`display:${display}`} class="modal">
    <!-- Modal content -->
    <div class="modal-content" style="width:{width}; margin-top:{top}">
        <div class="close" on:click={handleDialogClose}>&times;</div>
        <header>
            <slot name="header">
                <h2>Header</h2>
            </slot>
        </header>
        <div class="modal-body">
            <slot name="body">
                <h2>body</h2>
            </slot>
        </div>
        <footer>
            <slot name="footer">
                <h2>footer</h2>
            </slot>
        </footer>
    </div>

</div>
