<script>
    export let items = [];
    export let isOpened = false;
    import ModalDialog from "./ModalDialog.svelte";
    function parseObject(array) {
        let rows = [];
        let columns = [];
        for (let [key, value] of Object.entries(array[0])) {
            columns.push(key);
        }
        rows.push(columns);
        array.forEach(element => {
            columns = [];
            for (let [key, value] of Object.entries(element)) {
                columns.push(value);
            }
            rows.push(columns);
        });
        return rows;
    }
    $: table = items.length == 0 ? [] : parseObject(items);
</script>

<style>
    .body {
        margin: 0 auto;
        max-width: 90%;
        overflow-x: scroll;
    }
    table,
    td,
    th {
        border: 1px solid #ddd;
        text-align: left;
    }

    table {
        border-collapse: collapse;
        width: 100%;
    }
    th {
        background-color: #1a73e8;
        color: white;
    }

    th,
    td {
        width: auto;
        min-height: 40px;
        min-width: 40px;
        padding: 7px;
        white-space: nowrap;
    }
</style>

<ModalDialog bind:isOpened width="900px">
    <h2 slot="header">Worker</h2>
    <div slot="body" class="body">
        <table>
            {#each table as row, i}
                <tr>
                    {#each row as cell, j}
                        {#if i === 0}
                            <th>{cell}</th>
                        {:else}
                            <td>{cell}</td>
                        {/if}
                    {/each}
                </tr>
            {/each}
        </table>
    </div>
    <div slot="footer" />
</ModalDialog>
