<script>
    import { onMount } from "svelte";
    import { createEventDispatcher } from "svelte";
    import LocationPicker from "location-picker";
    import { isMapReady } from "../stores.js";
    import { geocodeAddress } from "../js/functions.js";

    let mapContainer;
    let map;
    let marker;
    let zoom = 8;
    let center = { lat: -34.397, lng: 150.644 };

    const dispatch = createEventDispatcher();

    let isMapReadyValue;
    let isMounted = false;
    let addressInput = {};

    let dropdownVisible = false;

    const unsubscribe = isMapReady.subscribe(v => {
        isMapReadyValue = v;
    });

    const getCors = () => {
        console.log(marker);
        return { lat: marker.position.lat(), lng: marker.position.lng() };
    };

    onMount(async () => {
        isMounted = true;
        dispatch("mount", { getCors });
    });
    $: {
        if (isMapReadyValue && isMounted) {
            console.log(google);
            console.log("map is ready");
            initializeMap(mapContainer);
            initAutocomplete();
        }
    }
    function initializeMap(canvas) {
        let lat = 50;
        let lng = 50;
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(position => {
                lat = position.coords.latitude;
                lng = position.coords.longitude;
                let myLatLng = new google.maps.LatLng(lat, lng),
                    myOptions = {
                        zoom: 15,
                        center: myLatLng,
                        mapTypeId: google.maps.MapTypeId.ROADMAP
                    },
                    newMap = new google.maps.Map(canvas, myOptions),
                    newMarker = new google.maps.Marker({
                        position: myLatLng,
                        map: map
                    });
                map = newMap;
                newMarker.setMap(map);
                marker = newMarker;
            });
        } else {
            alert("You`d better use modern web browser");
        }
    }
    function moveMarker(lat, lng) {
        //delayed so you can see it move
        marker.setPosition(new google.maps.LatLng(lat, lng));
        map.panTo(new google.maps.LatLng(lat, lng));
    }
    function handleDropdownChoice(chosenAddress) {
        const lat = chosenAddress.geometry.location.lat();
        const lng = chosenAddress.geometry.location.lng();
        moveMarker(lat, lng);
        console.log(JSON.stringify(chosenAddress));
    }
    function initAutocomplete() {
        // Create the autocomplete object, restricting the search predictions to
        // geographical location types.
        let autocomplete = new google.maps.places.Autocomplete(addressInput, {
            types: ["geocode"]
        });

        // Avoid paying for data that you don't need by restricting the set of
        // place fields that are returned to just the address components.
        autocomplete.setFields(["geometry"]);

        // When the user selects an address from the drop-down, populate the
        // address fields in the form.
        autocomplete.addListener("place_changed", () => {
            handleDropdownChoice(autocomplete.getPlace());
        });
    }
</script>

<style>
    .hidden {
        display: none;
    }
    .map {
        transition: 1s linear;
        height: 100%;
        width: 100%;
        margin-top: -50px;
    }
    .map-box {
        height: 100%;
        width: 100%;
    }
    .input-box {
        display: inline-block;
        height: 40px;
        padding: 3px;
        position: relative;
        left: 20px;
        z-index: 1;
    }
    .input-part {
        height: 100%;
        width: 100%;
    }
    .input-part > input {
        height: 100%;
        width: 200px;
        margin-bottom: 0;
    }
    .input-part > input:focus {
        outline: none;
    }
</style>

<div class="map-box">

    <div class="input-box">
        <div class="input-part">
            <input type="text" bind:this={addressInput} />
        </div>
    </div>
    <div bind:this={mapContainer} class:hidden={!isMapReadyValue} class="map" />
</div>
